import { NextResponse } from 'next/server';
import connectDB from '../../../lib/db';
import Trip from '../../../models/Trips';

export async function GET() {
  try {
    await connectDB();
    console.log('Fetching trips from database...'); // Debug log
    
    const trips = await Trip.find({ status: { $ne: 'draft' } });
    console.log('Found trips:', trips); // Debug log
    
    if (!trips || trips.length === 0) {
      console.log('No trips found'); // Debug log
      return NextResponse.json([]);
    }
    
    return NextResponse.json(trips);
  } catch (error) {
    console.error('Error in /api/trips:', error); // Debug log
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}