import mongoose from 'mongoose';

const tripSchema = new mongoose.Schema({
  title: {
    type: String,
    required: true
  },
  description: {
    type: String,
    required: true
  },
  destinationType: {
    type: String,
    required: true
  },
  destinationCountry: {
    type: String,
    required: true
  },
  destinationSeason: {
    type: String,
    required: true
  },
  duration: {
    type: Object
  },
  images: [{
    type: String
  }],
  itinerary: [{
    type: Object
  }],
  stays: [{
    type: Object
  }],
  pricing: [{
    type: Object
  }],
  features: [{
    type: Object
  }],
  status: {
    type: String,
    default: 'draft'
  },
  createdAt: {
    type: Date,
    default: Date.now
  },
  updatedAt: {
    type: Date,
    default: Date.now
  }
});

export default mongoose.models.Trip || mongoose.model('Trip', tripSchema, 'Test');